import { axiosClient } from "@rp/react/axios-client"
import { FILES_BASE_URL, FILES_ROOT } from "../file.constants"
import { FileURLData } from "../file.types"

const getFileUrl = async (id: string): Promise<FileURLData> => {
  const { data } = await axiosClient.get(
    `${FILES_BASE_URL}${FILES_ROOT}/${id}?status-code-override=true`,
  )

  return data
}

export { getFileUrl }
